// import { All, Detail, Home, Search, My } from "./pages/store";
// import PrivateRoute from "./components/utlis/PrivateRoute";
import { lazy } from "react";

// const Login = lazy(() => import("./pages/store/Login"));
const All = lazy(() => import("./pages/store/All"));
const Detail = lazy(() => import("./pages/store/Detail"));
const Home = lazy(() => import("./pages/store/Home"));
const Search = lazy(() => import("./pages/store/Search"));
const My = lazy(() => import("./pages/store/My"));
const PrivateRoute = lazy(() =>
  import("./components/utlis/PrivateRoute/index")
);

const AppRoutes = [
  {
    path: "/all",
    element: (
      <PrivateRoute>
        <All />
      </PrivateRoute>
    ),
  },
  {
    path: "/detail",
    element: (
      <PrivateRoute>
        <Detail />
      </PrivateRoute>
    ),
  },
  {
    path: "/home",
    element: (
      <PrivateRoute>
        <Home />
      </PrivateRoute>
    ),
  },
  {
    path: "/search",
    element: (
      <PrivateRoute>
        <Search />
      </PrivateRoute>
    ),
  },
  {
    path: "/my",
    element: (
      <PrivateRoute>
        <My />
      </PrivateRoute>
    ),
  },
  // {
  //   path: "/auth/login",
  //   element: <Login />,
  // },
];

export default AppRoutes;
